<template>
  <div>
    <headerBar :header-data="headerData" />
    <div v-if="pageStyles === 0" class="container">
      <div class="content">
        <div class="common-main box">
          <div class="notice-content" v-html="ggnr" />
        </div>
      </div>
    </div>
    <div v-else>
      <error-page
        :page-style="pageStyles"
        @errorButton="parentReceive"
      />
    </div>
  </div>

</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar'
import { mapState } from 'vuex'
import errorPage from '@/components/result-page/index'
export default {
  name: 'One',
  components: {
    headerBar,
    errorPage
  },
  data() {
    return {
      pageStyles: 0,
      ggnr: '',
      showMore: false,
      headerData: {
        title: '公告内容',
        leftUrl: 'back',
        leftText: ' '
      }
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      wybmListItem: state => state.gyswybm.wybmListItem
      // gysyqk: state => state.wybm.yqhck
    })
  },
  created() {
    this.ggid = this.$route.params.ggid
    this.getCqxx()
  },
  methods: {
    parentReceive() {
      this.getCqxx()
    },
    getCqxx() {
      this.$store.dispatch('gyswdxm/getGysGgnrAction', { ggid: this.ggid, zzid: this.userInfo.zzid }).then(res => {
        if (res) {
          this.ggnr = res
          this.pageStyles = 0
        } else {
          this.pageStyles = 1
        }
      }).catch((error) => {
        this.pageStyles = 3
        this.$toast(error)
      })
    },
    MoreAction(item) {
      console.log('MoreAction======', item)
      const more = []
      more.push(item.showmore)
      more.push(item.title)
      this.$router.push({
        name: 'DbsxSjbqMore',
        params: { morexx: more }
      })
    }
  }

}
</script>
<style lang="css" scoped>

</style>
